@import "mixins";


@media all  and (max-width: 968px) {

	.portfolio:before {
		background-size: cover; }

	.portfolio {
		.portfolio-course {
			.course {
				min-width: 220px;
				.p {
					font-size: 20px; }
				.img {
					width: 80%;
					height: 100px; } } } }

	.pricing {
		.pricing-content {
			.pricing-slot {
				min-width: 150px;
				.pricing-slot-img {
					height: 110px; }
				button {
					// position: relative
					padding-left: 15px;
					padding-right: 15px;
					width: 90%;
					right: 50%;
					@include translate(50%, 0); } } } }


	.about {
		.story {
			.story-school {
				flex-direction: column;
				.story-school-item {
					width: 90%; } } } } }


@media all  and (max-width: 767px) {

	.navbar {
		.navbar-collapse {
			background: rgba(59, 59, 59, 0.5); } }

	.contact {
		.contact-content {
			padding: 0 !important; }
		.contact-numbers {
			padding-left: 20px;
			padding-right: 20px; }
		.contact-form {
			position: relative;
			top: 0;
			right: 0;
			border-radius: 0;
			box-shadow: none;
			margin-bottom: 0; } }

	footer {
		    padding: 25px 0 15px; } }

@media all  and (max-width: 736px) {

	.portfolio:before {
		// @include rotate(90)
		top: 25px; }

	.portfolio {
		padding-bottom: 0; }

	.pricing {
		.pricing-content {
			.pricing-slot {
				min-width: 220px;
				.pricing-slot-img {
					height: 130px; } } } }
	.gallery-h:before {
		width: 50%; } }


@media all  and (max-width: 660px) {

	.nav-arrows span {
		width: 30px !important;
		height: 30px !important; }

	.gallery {
		.gallery-foto {
			.fancybox-button {
				.fancybox-caption {
					font-size: 12px; } } } } }

@media all  and (max-width: 506px) {

	.main-head-slider {
		.sl-slider-h {
			font-size: 60px;
			span:last-child {
				font-size: 20px; } } }

	.portfolio:before {
		background: none; }

	.portfolio {
		.portfolio-course {
			.course {
				min-width: 180px;
				.p {
					font-size: 16px;
					padding-left: 10px;
					padding-right: 10px; }
				.img {
					width: 60%;
					height: 60px; } } } }


	.pricing:before {
		top: 52%;
		height: 51%; }
	.pricing {
		.pricing-content {
			.pricing-slot {
				min-width: 180px;
				.pricing-slot-img {
					height: 120px; }
				.pricing-slot-name {
					h4 {
						padding: 5px 15px 35px; } } } } }

	.about {
		.story {
			.story-text {
				font-size: 20px;
				margin-bottom: 10px;
				.new {
					font-size: 50px; } }
			.story-school {
				.story-school-item {
					font-size: 22px;
					margin: 10px; } } } } }




@media all  and (max-width: 426px) {

	.portfolio {
		.portfolio-course {
			.course {
				min-width: 280px;
				.p {
					font-size: 24px;
					padding-left: 20px;
					padding-right: 20px; }
				.img {
					width: 80%;
					height: 80px; } } } }

	.pricing:before {
		top: 52%;
		height: 75%; }
	.pricing {
		.pricing-content {
			.pricing-slot {
				min-width: 280px;
				.pricing-slot-img {
					height: 70%; }
				.pricing-slot-name {
					h4 {
						padding: 5px 15px 35px; } } } } }
	.gallery {
		.gallery-foto {
			.fancybox-button {
				.fancybox-caption {
					font-size: 10px;
					letter-spacing: 1px; } } } }
	.contact {
		.contact-numbers {
			.contact-phone {
				span {
					font-size: 20px; } } } } }

@media all  and (max-width: 375px) {

	.main-head-slider {
		.sl-slider-h {
			font-size: 50px;
			span:last-child {
				font-size: 16px; } } } }
